#root {
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.wrapper {
  max-width: 1600px;
  margin: 0 auto; }
  .wrapper .switchLang img {
    margin: 5px;
    box-shadow: 2px 2px 10px gray; }
    .wrapper .switchLang img:hover {
      -webkit-transform: scale(1.2, 1.2);
              transform: scale(1.2, 1.2);
      cursor: pointer; }
    .wrapper .switchLang img:active {
      -webkit-transform: scale(1.1, 1.1);
              transform: scale(1.1, 1.1); }
  .wrapper .navbar-brand {
    padding-top: 20px !important; }

.hero {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  padding: 20px;
  box-shadow: 2px 2px 10px gray;
  color: #2b1705; }
  @media screen and (max-width: 640px) {
    .hero {
      min-width: 100%;
      padding: 20px;
      margin-bottom: 10px; } }
  .hero img {
    display: block;
    max-width: 300px;
    margin: 0 auto;
    padding-bottom: 10px; }
    @media screen and (max-width: 640px) {
      .hero img {
        min-width: 80%; } }
  .hero .title {
    font-family: "New", Arial, Helvetica, sans-serif;
    font-size: 1.2em;
    text-align: center;
    max-width: 300;
    margin: 0 auto; }

@font-face {
  font-family: "New";
  src: url(/codejam5/static/media/nefont.05be004b.ttf); }

.background {
  padding: 10px 5px;
  text-align: center;
  padding-top: 15vmin;
  padding-bottom: 20vmin;
  background-image: url(/codejam5/static/media/bel-mir.75af3aed.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 2px 2px 10px gray; }
  @media screen and (max-width: 640px) {
    .background {
      padding-top: 0;
      background-image: none; } }
  .background h1,
  .background h2 {
    font-family: New, Arial, Helvetica, sans-serif;
    color: #0e0701;
    text-shadow: 5px 5px 20px white; }
  .background h1 {
    font-size: 6vmin; }
  .background h2 {
    font-size: 4vmin; }

@font-face {
  font-family: "fontello";
  src: url(/codejam5/static/media/fontello.98641b00.woff);
  font-weight: normal;
  font-style: normal; }

.developers-title {
  text-align: center;
  border-top: 1px solid #e1dedb;
  padding-top: 50px; }

.developers-title:last-child {
  margin-top: 10px;
  border-top: 1px solid #e1dedb; }

.developers {
  display: flex;
  width: 100%;
  margin-top: 20px;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px 50px 50px 50px; }

.developers-teammate {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 15px;
  font-family: josefin;
  line-height: 1.2;
  background-color: #f8f9fa;
  border-radius: 15px;
  flex-basis: 250px;
  border: 1px solid #e1dedb;
  box-shadow: 0 0 5px rgba(33, 0, 87, 0.5); }

.teammate-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 20px;
  font-family: josefin bold;
  line-height: 1.5; }

.noselect {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
          user-select: none; }

.developers-teammate:nth-child(1) .teammate-foto {
  background: url(/codejam5/static/media/mdgh.092a527a.jpg);
  background-size: contain; }

.developers-teammate:nth-child(2) .teammate-foto {
  background: url(/codejam5/static/media/gaddyya.89a26951.jpg);
  background-size: contain; }

.developers-teammate:nth-child(3) .teammate-foto {
  background: url(/codejam5/static/media/zmichgen.955bb514.jpg);
  background-size: contain; }

.developers-teammate:nth-child(4) .teammate-foto {
  background: url(/codejam5/static/media/zerovers.c26d5d2a.jpg);
  background-size: contain; }

.developers-teammate:nth-child(5) .teammate-foto {
  background: url(/codejam5/static/media/renardby.c4666684.png);
  background-size: contain; }

.teammate-foto {
  width: 150px;
  height: 150px;
  margin: 10px 0;
  border-radius: 50%;
  border: 4px solid #141414; }

.linked-icon {
  display: inline-block;
  width: 1em;
  margin: 0 0.2em;
  font-family: "Fontello", Arial;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-align: center;
  text-transform: none;
  line-height: 1em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.linked-icon:hover {
  color: #e5194c; }

body {
  flex-direction: column;
  display: flex;
  align-items: center;
  margin: 0px; }

.footer {
  position: absolute;
  right: 0;
  padding: 0;
  text-align: center;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #000;
  color: #fff;
  overflow: hidden; }

.portal {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-bottom: 50px; }
  @media screen and (max-width: 640px) {
    .portal {
      flex-direction: column; } }

.about-author {
  padding: 50px 50px 50px 0px;
  display: flex;
  padding-left: 50px;
  background-color: #f8f9fa;
}

.about-author img {
  float: left;
  margin-right: 20px;
  border: 1px black solid;
}

.author-books {
  padding: 10px 0;
  padding-left: 42%;
  background-color: #f8f9fa;
}

.author-media {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-content: center;
  padding: 50px;
}

.image-gallery-thumbnail-inner .active {
  border: none;
}

.image-gallery {
  width: 50%;
  height: 360px;
}

.image-gallery-swipe {
  display: flex;
  justify-content: center;
}

.image-gallery-slide img {
  width: 480px;
  margin-top: 35px;
  height: 360px;
}

.image-gallery-content {
  position: relative;
  width: 100%;
  line-height: 0;
  top: 0;
}

.image-gallery-slide-wrapper {
  position: relative;
  width: 100%;
}

.youtube-video {
  padding: 35px;
  width: 50%;
  height: 400px;
}

.authors-container {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}

.author-info {
  max-width: 250px;
  width: 100%;
  text-align: center;
}

.author-info:hover {
  cursor: pointer;
  border: none;
}

.author-name {
  color: black;
  padding-bottom: 10px;
}

.author-foto {
  width: 195px;
  height: 190px;
}

.search-input input {
  width: 100% !important;
  outline-style: none;
}

.author-description {
  color: grey;
}

.authors-container a {
  text-decoration: none;
}

.authors-container a:hover {
  text-decoration: none;
}

div.author {
  width: 200px;
  margin: 10px;
}

